import logo from '../../assets/logo';
import iconDash from '../../assets/iconDash';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import React, { useState, useEffect } from 'react';
import LateralMenu from './LateralMenu';
import Cookies from 'universal-cookie';

function Dashboard() {
  const navigate = useNavigate();
  const [list, setList] = useState([]);
  const [supplierUp, setSupplierUp] = useState(false);
  const cookies = new Cookies();

  const nameUser = cookies.get('first_name');

  function newSupplier() {
    navigate('/suplier/add');
  }

  function showButton() {
    navigate('/supplier/edit');
  }

  function Logout() {
    cookies.remove('id', { path: '/' });
    cookies.remove('email', { path: '/' });
    cookies.remove('first_name', { path: '/' });

    navigate('/');
  }

  function supplierDelete(id) {
    Swal.fire({
      imageUrl: iconDash.warning,
      imageHeight: 100,
      imageWidth: 100,
      text: `¿Esta seguro que desea eliminar el proveedor?`,
      confirmButtonText: 'Eliminar',
      confirmButtonColor: '#0DB1AC',
      cancelButtonText: 'Cancelar',
      cancelButtonColor: '#FF5859',
      showCancelButton: true,
    }).then((result) => {
      if (result.value) {
        handleDelete(id);
      }
    });
  }

  const handleDelete = (id) => {
    const requestInit = {
      method: 'DELETE',
    };

    fetch(
      `https://dev.hubmine.mx/api/suppliers/delete/${id}`,
      requestInit
    ).then((response) => {
      if (response.status !== 404) {
        Swal.fire(
          'Operación exitosa!',
          'Se elimino correctamente el proveedor',
          'success'
        );
      } else {
        Swal.fire(
          'No se puede eliminar',
          'Al parecer tenemos problemas con nuestro servidor',
          'error'
        );
      }
      setSupplierUp(true);
    });
  };

  useEffect(() => {
    const getSpupplierList = () => {
      fetch('https://dev.hubmine.mx/api/suppliers/list/')
        .then((response) => response.json())
        .then((json) => {
          if (json === 404) {
            Swal.fire('Ups...', `${json.msg}`, 'error');
          } else {
            setList(json);
          }
        });
    };
    getSpupplierList();
    setSupplierUp(false);
  }, [supplierUp]);

  useEffect(() => {
    if (!cookies.get('email')) {
      navigate('/');
    }
  }, []);

  return (
    <>
      <nav className='nav flex items-center justify-around'>
        <div className='flex items-center space-x-3'>
          <img className='logo' src={logo.logo} alt='logo hubmine' />
          <div>
            <h1 className='title-hub'>
              Hub<span className='span-mine'>mine</span>
            </h1>
            <h6 className='text-textgray font-semibold'>Supplier Management</h6>
          </div>
        </div>
        <div className='relative flex w-1/2 flex-wrap items-stretch mt-2 mb-3'>
          <span className='span-image'>
            <img src={iconDash.search} alt='' />
          </span>
          <input
            autoComplete='off'
            type='text'
            className='input-primary '
            placeholder='Buscar'
          />
        </div>
        <div>
          <button
            onClick={newSupplier}
            className='bg-primary w-24 p-3 rounded-lg text-textwhite text-sm cursor-pointer'>
            Añadir
          </button>
        </div>
        <div className='flex items-center space-x-3'>
          {/* <h1>{`${nameUser}`}</h1> */}
          <button className='w-10 h-10 rounded-full bg-primary text-textwhite cursor-pointer '>
            {/* {`${nameUser[0]}`} */}
          </button>
          <button
            onClick={() => Logout()}
            className='w-10 h-10  bg-primary text-textwhite cursor-pointer '>
            Salir
          </button>
        </div>
      </nav>
      <main className='w-full h-auto flex'>
        <section className='w-60 border-r-2 border-gray'>
          <LateralMenu />
        </section>
        <section className='w-full h-auto'>
          <table className='w-full mt-4'>
            <thead>
              <tr className='h-10'>
                <th className='text-header'>ID</th>
                <th className='text-header'>PROVEEDOR</th>
                <th className='text-header'>TELÉFONO</th>
                <th className='text-header'>EMAIL</th>
                <th className='text-header'>CONTACTO</th>
                <th className='text-header'>VER</th>
                <th className='text-header'>DELETE</th>
              </tr>
            </thead>
            <tbody>
              {list.map((listSupp) => (
                <tr
                  key={listSupp.id}
                  className='w-full h-20 border-y-2 border-gray1 text-center  text-textblack hover:bg-secondary'>
                  <td>{listSupp.id}</td>
                  <td className='uppercase'>{listSupp.supplier_name}</td>
                  <td>{listSupp.supplier_phone}</td>
                  <td>{listSupp.supplier_email}</td>
                  <td className='capitalize'>{listSupp.contact_name}</td>
                  <td>
                    <div>
                      <button onClick={showButton} className='button-show'>
                        <img src={iconDash.eyeWhite} alt='bote de basura' />
                      </button>
                    </div>
                  </td>
                  <td>
                    <div>
                      <button
                        className='button-trash'
                        onClick={() => supplierDelete(listSupp.id)}>
                        <img src={iconDash.trashCan} alt='bote de basura' />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </section>
      </main>
    </>
  );
}

export default Dashboard;
